.Container {
    display: flex;
    flex-direction: column;
    text-align: center;
  }
#paragraph1,
#paragraph2 {
  max-width: 100vw; /* Adjust as needed */
  font-size: 20px;
  color: whitesmoke;
}

#paragraph2 {
  margin-top: 30px;
}

.comic {
    width: 200px; /* Adjust image size as needed */
    height: auto;
    object-fit: cover;
    border-radius: 10px;
    
  }
  
.picture {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 10px;
    margin-top: 20px;
  }
#thanks{
    margin-top: 20px;
    text-align: center;
    font-size: 35px;
    color: #54b3b0;
}
.grid-item {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
  }
  
  #button {
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    background-color: #007bff;
    color: white;
    font-size: 16px;
    cursor: pointer;
  }